.containerReservasiEdit {
    margin: 2.5rem auto;
    max-width: 800px;
    padding: 2rem;
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
}

.containerReservasiEdit h1 {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
}

.formReservasiEdit {
    display: flex;
    flex-direction: column;
}

.formGroupReservasiEdit {
    margin-bottom: 1rem;
}

.formGroupReservasiEdit label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
    color: #4a5568; 
}

.inputReservasiEdit {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #cbd5e0; 
    border-radius: 4px;
    font-size: 1rem;
    color: #4a5568; 
    transition: border-color 0.2s ease;
}

.inputReservasiEdit:focus {
    outline: none;
    border-color: #3182ce;
    box-shadow: 0 0 5px rgba(49, 130, 206, 0.5); 
}

.btnReservasiEdit {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 4px;
    cursor: pointer;
    background-color: #4299e1; 
    color: white;
    border: none;
    margin-top: 1rem;
}

.btnReservasiEdit:hover {
    background-color: #2b6cb0; 
}
